const deleteItem = item => {
  let regular_list = store.getState().common.regular_list;
  // log(item.collection_word_id, regular_list);

  regular_list = regular_list.map(word => {
    if (word.collection_word_id === item.collection_word_id) {
      word.is_deleted = true;
    }
    return word;
  });
  // removeList.push(item.collection_word_id);

  // return;
  let self = this;
  // return;
  confirm({
    title: "你是否要删除该单词?",
    okText: "确认",
    cancelText: "取消",
    onOk() {
      deletRequest(`${routePath}/${item.collection_word_id}`)
        .then(res => {
          // log(res);
          store.dispatch({
            type: "SET_COMMON",
            value: { regular_list }
          });
          message.success("删除成功", () => {});
        })
        .catch(err => {
          log(err);
        });
    },
    onCancel() {
      message.success("已取消");
    }
  });
};
const etymaWord = text => (
  <div>
    {text.split("\n").map((i, key) => {
      return <div key={key}>{i}</div>;
    })}
  </div>
);
const imageHost = "https://jiguangdanci.easylesson.cn";
