import React, { Component } from "react";
import { setValue } from "@/redux/keke-collection";
import { connect } from "react-redux";
import {
  Form,
  Row,
  Col,
  Input,
  Button,
  Icon,
  message,
  Divider,
  Tag,
  Tooltip,
  Pagination,
  InputNumber
} from "antd";
import Filter from "co/filter";
import Table from "co/table";
import "./index.less";
import { get, post } from "util/fetch";

import { filters, columns } from "./data";

const routePath = "/api/english/keke-collection";
class index extends Component {
  state = {
    searchButtonLoading: false,
    source: [],
    pagination: {},
    filterResult: {},
    tokenExpired: 5,
    shareLink: ""
  };

  componentDidMount() {
    const pagination = {
      current: this.props.kekeCollection.pagination.current,
      pageSize: this.props.kekeCollection.pagination.pageSize,
      showSizeChanger: true,
      onShowSizeChange: this.onPaginationSizeChange,
      onChange: this.onPaginationChange,
      showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
    };
    const { filterResult } = this.props.kekeCollection;
    this.state.pagination = pagination;
    this.state.filterResult = filterResult;
    this.updateTable();

    // 初始化粘贴板
    let clipboard = new ClipboardJS("#clip");

    clipboard.on("success", function(e) {
      message.success("复制成功");
    });
  }
  onPaginationSizeChange = (current, size) => {
    let { pagination, filterResult } = this.state;
    pagination.pageSize = size;
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  onPaginationChange = (page, pageSize) => {
    let { pagination, filterResult } = this.state;
    pagination = { ...pagination, current: page, pageSize };
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  filterResult = values => {
    let { pagination, filterResult } = this.state;
    pagination.current = 1;
    filterResult = values;
    Object.keys(filterResult).forEach(item => {
      if (!filterResult[item]) {
        filterResult[item] = "";
      }
    });
    this.state.filterResult = filterResult;
    this.props.setValue({ pagination, filterResult });
    this.state.pagination = pagination;
    this.updateTable();
  };
  updateTable = () => {
    const { pagination, filterResult } = this.state;
    if (!this.Interceptor()) {
      return;
    }
    this.setState({
      searchButtonLoading: true
    });
    let params = {
      pageNo: pagination.current,
      pageSize: pagination.pageSize,
      ...filterResult
    };
    // params = Object.assign({}, params, values);
    get(routePath, params)
      .then(res => {
        pagination.total = res.count;
        const source = (res.data || []).map((item, index) => {
          const temp = {
            // key: item.id,
            key: (pagination.current - 1) * pagination.pageSize + index + 1,
            collection_id: item.id,
            ...item
          };
          return temp;
        });
        this.setState({
          source,
          pagination,
          searchButtonLoading: false
        });
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          searchButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  Interceptor = () => {
    return true;
  };

  createItem = () => {
    this.props.history.push("/keke-collection/create");
  };
  generateCollectionPageLink = () => {
    let time = this.state.tokenExpired * 24 * 60 * 60;
    let url = `/api/common/expired-token?expires=${time}`;
    log(this.state.filterResult.name);
    let self = this;
    get(url)
      .then(res => {
        // setStore("name", res.data.token);
        message.success("生成成功");
        let host = ["http://word.liangyl.com/", "http://localhost:9091"];
        let link = `${host[0]}#/keke-collection?collection_name=${
          this.state.filterResult.name
        }&token=${res.data.token}`;
        self.setState({
          shareLink: link
        });
      })
      .catch(err => {
        log(err);
      });
  };
  onInputNumberChange = value => {
    this.setState({
      tokenExpired: value
    });
    log(value);
  };
  render() {
    const {
      searchButtonLoading,
      source,
      pagination,
      filterResult
    } = this.state;
    const array = { filters, searchButtonLoading, values: filterResult };
    const tableData = { columns, source, pagination };
    return (
      <div className="keke-collection">
        <Filter
          array={array}
          filterResult={this.filterResult}
          createItem={this.createItem}
        />
        <div className="manager">
          <div className="title">
            <div className="icon" />
            词库管理
          </div>
          <div className="manage-cell">
            <div className="cell">
              <div>
                分享该词库：选择有效时间0-2天{" "}
                <InputNumber
                  min={0.5}
                  max={7}
                  step={0.5}
                  defaultValue={this.state.tokenExpired}
                  onChange={this.onInputNumberChange}
                />
                <Button
                  onClick={this.generateCollectionPageLink}
                  type="primary"
                >
                  提交
                </Button>
              </div>
              <p>备注：点击生成词库的分享链接</p>
              <div>
                {this.state.shareLink === "" ? null : (
                  <div>
                    <Button
                      id="clip"
                      type="primary"
                      data-clipboard-target="#bar"
                    >
                      复制
                    </Button>
                    <p id="bar">{this.state.shareLink}</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>

        <Table data={tableData} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  kekeCollection: state.kekeCollection
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
