import React, { Component } from "react";

import "./index.less";
import { itemArray } from "./data";
import Form from "co/form";
import { get, post, put, deletRequest } from "util/fetch";
import { message, Button, InputNumber, Select } from "antd";
import { setStore, getStore, removeStore } from "util/store";

import "clipboard";
import { connect } from "react-redux";

const routePath = "/api/exam_boot/teacher-collection";
const routeUserPath = "/api/exam_boot/user";
const routeWordsPath = "/api/exam_boot/teacher-collection-question";

const Option = Select.Option;
import { columns } from "./data";
import Table from "co/table";

class index extends Component {
  state = {
    minHeight: window.innerHeight - 115,
    confirmButtonLoading: false,
    type: "create", // create and edit
    simpleId: "",
    data: {},
    wordsData: [],
    pagination: {},
    tokenExpired: 7,
    shareLink: "",
    userList: []
  };
  componentDidMount() {
    this.init();
  }

  init = () => {
    const pagination = {
      current: 1,
      pageSize: 10,
      showSizeChanger: true,
      onShowSizeChange: this.onPaginationSizeChange,
      onChange: this.onPaginationChange,
      showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
    };
    this.state.pagination = pagination;

    let pathname = this.props.location.pathname;
    let pathnameList = pathname.split("/");
    let type = pathnameList[pathnameList.length - 1];

    let query = this.props.location.search;
    query = query.split("?");
    let simpleId = query[1];
    this.setState({
      simpleId,
      type
    });
    if (type === "edit") {
      this.getWordInfo(simpleId);
      this.getCollectionWords(simpleId);
      this.getUserList();
    }

    // 初始化粘贴板
    let clipboard = new ClipboardJS("#clip");

    clipboard.on("success", function(e) {
      message.success("复制成功");
    });
  };
  onPaginationSizeChange = (current, size) => {
    let { pagination, simpleId } = this.state;
    pagination.pageSize = size;
    this.state.pagination = pagination;

    this.getCollectionWords(simpleId);
  };
  onPaginationChange = (page, pageSize) => {
    let { pagination, simpleId } = this.state;
    pagination = { ...pagination, current: page, pageSize };
    this.state.pagination = pagination;

    this.getCollectionWords(simpleId);
  };
  getWordInfo = simpleId => {
    const url = `${routePath}/${simpleId}`;
    get(url)
      .then(res => {
        let data = res.data[0] === undefined ? {} : res.data[0];

        this.setState({
          confirmButtonLoading: false,
          data
        });
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  getUserList = () => {
    const url = `${routeUserPath}`;
    get(url)
      .then(res => {
        log(22, res.data);
        this.setState({
          userList: res.data
        });
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  getCollectionWords = simpleId => {
    const { pagination, type } = this.state;

    const url = `${routeWordsPath}`;
    const params = {
      collection_id: simpleId,
      equal: 1,
      pageNo: pagination.current,
      pageSize: pagination.pageSize
    };
    get(url, params)
      .then(res => {
        log(1123, res);
        let data = (res.data || []).map((item, index) => {
          // log(this.props.removeList);
          const temp = {
            key: item.id,
            // id: numbertostring(index + 1),
            index: (pagination.current - 1) * pagination.pageSize + index + 1,
            collection_id: simpleId,
            collection_question_id: item.collection_question_id,
            type,
            is_deleted: false,
            ...item
          };
          return temp;
        });

        store.dispatch({
          type: "SET_COMMON",
          value: { regular_list: data }
        });
        pagination.total = res.count;
        this.setState({
          total: res.count,
          wordsData: data,
          pagination
        });
        log(32, data);
      })
      .catch(err => {});
  };
  filterResult = (err, values) => {
    if (err) {
      return;
    }
    const { type, simpleId } = this.state;
    if (type === "edit") {
      this.updateWord(simpleId, values);
    } else if (type === "create") {
      this.createWord(values);
    }
  };
  updateWord = (simpleId, values) => {
    this.setState({
      confirmButtonLoading: true
    });
    put(`${routePath}/${simpleId}`, values)
      .then(res => {
        this.setState({
          confirmButtonLoading: false
        });
        message.success("更改成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  createWord = values => {
    this.setState({
      confirmButtonLoading: true
    });
    post(`${routePath}`, values)
      .then(res => {
        this.setState({
          confirmButtonLoading: false
        });
        this.props.history.go(-1);
        message.success("新增成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };

  addOneWord = () => {
    let params = `collection_id=${this.state.simpleId}`;
    this.props.history.push("/teacher-collection/choose?" + params);
  };

  addBatchWord = () => {
    let params = `collection_id=${this.state.simpleId}`;
    this.props.history.push("/collection-english/batch?" + params);
  };

  removeCollection = () => {
    let { simpleId, wordsData } = this.state;
    log(12, simpleId, wordsData, this.state.wordsData.length);
    deletRequest(`${routePath}/${simpleId}`)
      .then(res => {
        log(res);
        message.success("删除成功");
      })
      .catch(err => {
        log(err);
        message.info("删除失败");
      });
  };
  generateCollectionPageLink = () => {
    let time = this.state.tokenExpired * 24 * 60 * 60;
    let url = `/api/common/expired-token?expires=${time}`;
    let self = this;
    get(url)
      .then(res => {
        // setStore("token", res.data.token);
        message.success("生成成功");
        let host = ["http://word.liangyl.com/", "http://localhost:9091"];
        let link = `${host[0]}#/exam-collection?collection_id=${
          this.state.simpleId
        }&token=${res.data.token}&user_id=${this.state.user_id}`;
        self.setState({
          shareLink: link
        });
      })
      .catch(err => {
        log(err);
      });
  };
  onInputNumberChange = value => {
    this.setState({
      tokenExpired: value
    });
    log(value);
  };
  handleSelectChange = value => {
    this.setState({
      user_id: value
    });
  };
  render() {
    const {
      minHeight,
      simpleId,
      data,
      confirmButtonLoading,
      wordsData,
      pagination
    } = this.state;
    const array = {
      itemArray,
      confirmButtonLoading,
      values: data
    };

    const tableData = { columns, source: wordsData, pagination };
    return (
      <div className="collection-edit-container" style={{ minHeight }}>
        <div className="title">
          <div className="icon" />
          词库编辑
        </div>
        <Form array={array} filterResult={this.filterResult} />
        <div className="title  margin-20">
          <div className="icon" />
          词库管理
        </div>
        <div className="manage-cell">
          <div className="cell">
            <div>
              删除该词库：
              <Button
                type="primary"
                disabled={this.state.wordsData.length !== 0}
                onClick={this.removeCollection}
              >
                点击
              </Button>
            </div>
            <p>备注：谨慎操作，仅当该词库内单词全部删除，方能删除词库</p>
          </div>
          <div className="cell">
            <div>
              分享该词库：选择有效时间0-5天{" "}
              <InputNumber
                min={0.5}
                max={10}
                step={0.5}
                defaultValue={this.state.tokenExpired}
                onChange={this.onInputNumberChange}
              />
              <Select
                defaultValue={
                  this.state.userList.length === 0
                    ? ""
                    : this.state.userList[0].id
                }
                style={{ width: 120 }}
                onChange={this.handleSelectChange}
              >
                {this.state.userList.map(item => (
                  <Option key={JSON.stringify(item)} value={item.id}>
                    {item.username}
                  </Option>
                ))}
              </Select>
              <Button onClick={this.generateCollectionPageLink} type="primary">
                提交
              </Button>
            </div>
            <p>备注：点击生成词库的分享链接</p>
            <div>
              {this.state.shareLink === "" ? null : (
                <div>
                  <Button id="clip" type="primary" data-clipboard-target="#bar">
                    复制
                  </Button>
                  <p id="bar">{this.state.shareLink}</p>
                </div>
              )}
            </div>
          </div>
        </div>
        <div className="title margin-20">
          <div className="icon" />
          词汇管理 <span>[Total: {this.state.total} 个单词]</span>
          <div className="btn-add">
            <Button onClick={this.addBatchWord}>批量添加</Button>
            <Button type="primary" onClick={this.addOneWord}>
              添加
            </Button>
          </div>
        </div>

        <Table data={tableData} />
      </div>
    );
  }
}
const mapStateToProps = state => ({
  regular_list: state.common.regular_list
});

export default connect(mapStateToProps)(index);
