import React, { Component } from "react";
import { get, post, deletRequest } from "util/fetch";
import "./index.less";
import { message, Button, Modal } from "antd";

const routePath = "/api/english/keke";
const { confirm } = Modal;

export default class index extends Component {
  state = {
    minHeight: window.innerHeight - 115,
    wordId: "",
    data: {}
  };
  componentDidMount() {
    let query = this.props.location.search;
    query = query.split("?");
    let wordId = query[1];
    this.setState({
      wordId
    });
    this.updateWord(wordId);
  }
  updateWord = wordId => {
    get(`${routePath}/${wordId}`)
      .then(res => {
        let phonetic_sound = new Audio(res.data[0].pronunciation_url);
        this.setState({
          searchButtonLoading: false,
          data: res.data[0] === undefined ? {} : res.data[0],
          phonetic_sound
        });

        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          searchButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };

  gotoLastPage = () => {
    this.props.history.go(-1);
  };

  deleteItem = () => {
    let { wordId } = this.state;
    let self = this;
    confirm({
      title: "你是否要删除该单词?",
      okText: "确认",
      cancelText: "取消",
      onOk() {
        deletRequest(`${routePath}/${wordId}`)
          .then(res => {
            log(res);
            message.success("删除成功", () => {
              // self.props.history.go(-1);
            });
          })
          .catch(err => {
            log(err);
          });
      },
      onCancel() {
        message.success("已取消");
      }
    });
  };

  render() {
    const { minHeight, wordId, data } = this.state;
    log("lian", data);
    return (
      <div className="keke-detail-container" style={{ minHeight }}>
        <div className="title">
          <div className="icon" />
          单词详情
        </div>
        <div className="cell">
          <span className="key">单词</span>
          <span>{data.word}</span>
        </div>
        <div className="cell">
          <span className="key">单词ID</span>
          <span>{data.id}</span>
        </div>

        <div className="cell">
          <span className="key">定义</span>
          <span>{data.definition}</span>
        </div>

        <div className="cell">
          <span className="key">音标</span>
          <span>[{data.phonetic_symbol}]</span>
        </div>

        <div className="cell">
          <span className="key">音标发音</span>

          <div>
            <img
              style={{ width: 30, cursor: "pointer" }}
              src="/images/sound.png"
              title={data.pronunciation_url}
              onClick={() => {
                this.state.phonetic_sound.play();
              }}
            />
          </div>
        </div>

        <Button
          type="primary"
          onClick={this.deleteItem}
          disabled={data.is_checked}
        >
          删除
        </Button>
      </div>
    );
  }
}
