import React, { Component } from "react";
import { get, post, deletRequest } from "util/fetch";
import "./index.less";
import { message, Button, Modal } from "antd";

const routePath = "/api/english/regular";
const { confirm } = Modal;

const imageHost = "https://jiguangdanci.easylesson.cn";
export default class index extends Component {
  state = {
    minHeight: window.innerHeight - 115,
    simpleId: "",
    data: {}
  };
  componentDidMount() {
    let query = this.props.location.search;
    query = query.split("?");
    let simpleId = query[1];
    this.setState({
      simpleId
    });
    this.updateWord(simpleId);
  }
  updateWord = simpleId => {
    get(`${routePath}/${simpleId}`)
      .then(res => {
        let phonetic_sound = new Audio(
          imageHost + "/" + res.data[0].pronunciation_url
        );

        let sententce_sound = new Audio(
          imageHost + "/" + res.data[0].sample_sentence_audio
        );
        this.setState({
          searchButtonLoading: false,
          data: res.data[0] === undefined ? {} : res.data[0],
          phonetic_sound,
          sententce_sound
        });

        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          searchButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };

  gotoLastPage = () => {
    this.props.history.go(-1);
  };

  deleteItem = () => {
    let { simpleId } = this.state;
    let self = this;
    confirm({
      title: "你是否要删除该单词?",
      okText: "确认",
      cancelText: "取消",
      onOk() {
        deletRequest(`${routePath}/${simpleId}`)
          .then(res => {
            log(res);
            message.success("删除成功", () => {
              self.props.history.go(-1);
            });
          })
          .catch(err => {
            log(err);
          });
      },
      onCancel() {
        message.success("已取消");
      }
    });
  };

  mutilpleLine = text => (
    <div>
      {text == undefined
        ? null
        : text.split("\n").map((i, key) => {
            return <div key={key}>{i}</div>;
          })}
    </div>
  );
  render() {
    const { minHeight, simpleId, data } = this.state;
    log(data);
    return (
      <div className="regular-detail-container" style={{ minHeight }}>
        <div className="title">
          <div className="icon" />
          单词详情
        </div>
        <div className="cell">
          <span className="key">单词</span>
          <span>{data.word}</span>
        </div>
        <div className="cell">
          <span className="key">单词ID</span>
          <span>{data.id}</span>
        </div>

        <div className="cell">
          <span className="key">定义</span>
          <span>{data.definition}</span>
        </div>

        <div className="cell">
          <span className="key">视频</span>
          <div>
            {" "}
            <video width="300" height="auto" src={data.video_path} controls />
          </div>
        </div>
        <div className="cell">
          <span className="key">音标</span>
          <span>[{data.phonetic_symbol}]</span>
        </div>

        <div className="cell">
          <span className="key">音标发音</span>

          <div>
            <img
              style={{ width: 30, cursor: "pointer" }}
              src="/images/sound.png"
              title={data.pronunciation_url}
              onClick={() => {
                this.state.phonetic_sound.play();
              }}
            />
          </div>
        </div>

        <div className="cell">
          <span className="key">图标</span>
          {/* <span>{data.profile_picture_url}</span> */}
          <div>
            <img
              src={imageHost + data.profile_picture_url}
              title={data.profile_picture_url}
            />
          </div>
        </div>

        <div className="cell">
          <span className="key">例句</span>
          <span>{data.sample_sentence}</span>
        </div>

        <div className="cell">
          <span className="key">例句发音</span>
          <div>
            <img
              style={{ width: 30, cursor: "pointer" }}
              src="/images/sound.png"
              title={data.sample_sentence_audio}
              onClick={() => {
                this.state.sententce_sound.play();
              }}
            />
          </div>
        </div>

        <div className="cell">
          <span className="key">例句翻译</span>
          <span>{data.sample_sentence_translation}</span>
        </div>

        <div className="cell">
          <span className="key">例句出处</span>
          <span>{data.sample_sentence_source || "--"}</span>
        </div>

        <div className="cell">
          <span className="key">定义2</span>
          <span>{data.definition_2 || "--"}</span>
        </div>

        <div className="cell">
          <div className="key">例句2</div>
          <div>{this.mutilpleLine(data.sample_sentence_2)}</div>
        </div>

        <div className="cell">
          <span className="key">词根</span>
          <span>{data.etyma || "--"}</span>
        </div>

        <div className="cell">
          <span className="key">词组</span>
          <span>{data.word_group || "--"}</span>
        </div>

        <div className="cell">
          <span className="key">词态</span>
          <span>{data.morphology || "--"}</span>
        </div>

        <div className="cell">
          <div className="key">英语定义</div>
          <div>{this.mutilpleLine(data.english_definition)}</div>
        </div>

        <Button onClick={this.gotoLastPage}>返回</Button>
        <Button
          type="primary"
          onClick={this.deleteItem}
          disabled={data.is_checked}
        >
          删除
        </Button>
      </div>
    );
  }
}
